.card {
    display: flex;
    flex-direction: column;
    height: 520px;
    background-color: hsl(235, 46%, 20%);
    grid-area: 1/1/3/2;
    border-radius: 20px;
}

.person {
    flex: 2.5;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: left;
    padding-left: 30px;
    background-color: hsl(246, 80%, 60%);
    border-radius: 20px;
}

.person img {
    width: 75px;
    border: 2px solid white;
    border-radius: 50%;
    margin: 35px 0px 40px;
}

.person p {
    font-size: 15px;
    font-weight: 400;
    color: hsl(236, 100%, 87%);
    margin-bottom: 5px;
}

.person h2 {
    font-size: 42px;
    font-weight: 200;
    color: white;
}

.interval {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    padding-left: 30px;
    flex: 1;
    background-color: hsl(235, 46%, 20%);
    border-radius: 20px;
}

.interval a {
    text-decoration: none;
    display: block;
    color: hsl(246, 80%, 60%);
    font-size: 16px;
    font-weight: 400;
    margin: 10px 0px;
    cursor: pointer;
}

a.active {
    color: white;
}