.grid {
    width: 90vw;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 25px;
}

@media screen and (max-width: 1000px) {
    .grid {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(8, 1fr);
        margin: 20px 0px;
    }
  }