.stats {
    display: flex;
    flex-direction: column;
    border-radius: 20px;
}

.background {
    position: relative;
    flex: 1;
    background-color: hsl(15, 100%, 70%);
    border-radius: 20px 20px 0px 0px;
    overflow: hidden;
}

.background img {
    position: absolute;
    right: 12px;
    top: -12px;
}

.info {
    display: flex;
    justify-content: center;
    align-items: left;
    padding: 10px 30px;
    flex-direction: column;
    flex: 4;
    background-color: hsl(235, 46%, 20%);
    border-radius: 20px;
    z-index: 5;
}

.info:hover {
    background-color: hsl(237, 45%, 30%);
    cursor: pointer;
}

.info .header {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.info .header p  {
    font-size: 18px;
    font-weight: 500;
    color: white;
    margin-bottom: 15px;
}

.info .header .dots svg:hover {
    color: white;
    cursor: pointer;
}

.info .time {
    font-size: 56px;
    font-weight: 200;
    color: white;
    margin-bottom: 15px;
}

.info .conclusion  {
    font-size: 14px;
    color: hsl(236, 100%, 87%);
}

.lrw {
    background-color: hsl(15, 100%, 70%);
}
.sbp {
    background-color: hsl(195, 74%, 62%);
}
.lrs {
    background-color: hsl(348, 100%, 68%);
}
.lge {
    background-color: hsl(145, 58%, 55%);
}
.vs {
    background-color: hsl(264, 64%, 52%);
}
.sos {
    background-color: hsl(43, 84%, 65%);
}